import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../utils/Common";
import axios from "axios";
import Modal from "react-bootstrap/Modal";
import ViewHotelMarkup from "./ViewHotelMarkup";
import EditHotelMarkup from "./EditHotelMarkup";

const HotelMarkup = () => {
  const [markup, setMarkup] = useState(null);

  async function ReadHotelMarkup() {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/hotel-markup";

      let res = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      });

      if (res.status === 200) {
        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          // console.log(res.data.recordset);
          setMarkup(res.data.recordset);
        }
      }
    } catch (e) {
      errorToast(e.message);
    }
  }

  useEffect(() => {
    ReadHotelMarkup().then();
  }, []);

  /* ======================================================= */
  const [showEdit, setShowEdit] = useState(false);
  const handleCloseEdit = () => setShowEdit(false);
  const handleShowEdit = () => setShowEdit(true);

  const [detailEdit, setDetailsEdit] = useState(null);

  const editHandler = (markup_details) => {
    // console.log(markup_details);
    setDetailsEdit(markup_details);
    handleShowEdit();
  };

  /* ======================================================= */

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [detail, setDetails] = useState(null);

  /* Show Markup Details */
  const showMarkupDetails = (markup_details) => {
    // console.log(markup_details);
    setDetails(markup_details);
    handleShow();
  };

  /* ======================================================= */

  /* Delete Handler- Markup */
  const markupDeleteHandler = async (ID) => {
    if (confirm("Are you sure?")) {
      try {
        let token = getCookieValue(tokenName);
        let url = ipAddress + "admin/hotel-markup/" + ID;

        let res = await axios.delete(url, {
          headers: {'Authorization': `Bearer ${token}`}
        });
        // console.log(res);

        if (res.status === 200) {
          if (res.data.error) {
            errorToast(res.data.message);
          } else {
            // console.log(res.data);
            successToast(res.data.message);
            ReadHotelMarkup().then();
          }
        }
      } catch (e) {
        errorToast(e.message);
      }
    }
  };

  return (
    <>
      <CCard>
        <CCardHeader className="card-header-bg">
          <div className="row">
            <div className="col-md-8">
              <h4 className="mb-0 primary-color">Hotel Markup</h4>
            </div>
            <div className="col-md-4 text-md-end">
              <Link to="/create-hotel-markup" className="btn btn-primary">
                <i className="fa fa-plus"></i> Create Markup
              </Link>
            </div>
          </div>
        </CCardHeader>

        <CCardBody>
          {markup ? markup.length > 0 &&
            <ViewHotelMarkup data={markup} click={editHandler} showMarkupDetails={showMarkupDetails}
                             markupDeleteHandler={markupDeleteHandler}/> :
            <div className="text-center">
              <i className="spinner spinner-border"></i>
            </div>
          }
        </CCardBody>
      </CCard>

      {/* Modal - Markup Details */}
      <Modal show={show} onHide={handleClose} centered scrollable size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Markup Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="col-6 text-end">
              <p>Trip Type:</p>
            </div>
            <div className="col-6">
              <p>{detail?.TripType}</p>
            </div>

            <div className="col-6 text-end">
              <p>Star Rating:</p>
            </div>
            <div className="col-6">
              <p>{detail?.Star}</p>
            </div>

            <div className="col-6 text-end">
              <p>Markup Type:</p>
            </div>
            <div className="col-6">
              <p>{detail?.MarkupType === "F" ? "Fixed" : "Percentage"}</p>
            </div>

            <div className="col-6 text-end">
              <p>Markup Amount:</p>
            </div>
            <div className="col-6">
              <p>{detail?.MarkupAmount}</p>
            </div>

            <div className="col-6 text-end">
              <p>Agent Id:</p>
            </div>
            <div className="col-6">
              <p>{detail?.AgentId}</p>
            </div>

            <div className="col-6 text-end">
              <p>Agent Type:</p>
            </div>
            <div className="col-6">
              <p>{detail?.AgentType}</p>
            </div>

            <div className="col-6 text-end">
              <p>Provider:</p>
            </div>
            <div className="col-6">
              <p>{detail?.Provider}</p>
            </div>

            <div className="col-6 text-end">
              <p>Applied On:</p>
            </div>
            <div className="col-6">
              <p>{detail?.AppliedOn}</p>
            </div>

            <div className="col-6 text-end">
              <p>Submit By:</p>
            </div>
            <div className="col-6">
              <p>{detail?.SubmitBy}</p>
            </div>

            <div className="col-6 text-end">
              <p>Create Date:</p>
            </div>
            <div className="col-6">
              <p>{detail?.SubmitDate}</p>
            </div>

            <div className="col-6 text-end">
              <p className="mb-0">Update Date:</p>
            </div>
            <div className="col-6">
              <p>{detail?.UpdateDate}</p>
            </div>
          </div>
        </Modal.Body>
      </Modal>

      {/* Modal- Edit Markup */}
      <Modal show={showEdit} onHide={handleCloseEdit} centered scrollable size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Edit Markup</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <EditHotelMarkup detailEdit={detailEdit} ReadHotelMarkup={ReadHotelMarkup}/>
        </Modal.Body>
      </Modal>
    </>
  )
}

export default HotelMarkup;
