import React, {useMemo} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "../dashboard/agents/FilterComponent";

const ViewHotelMarkup = (props) => {
  const columns = [
    {
      name: "",
      button: true,
      cell: row => <div>
        <span onClick={() => props.click(row)} className="cell-links text-primary fw-bold">
          Edit
        </span>

        <span onClick={() => props.markupDeleteHandler(row.ID)} className="cell-links text-primary fw-bold mx-2">
          Delete
       </span>

        <span onClick={() => props.showMarkupDetails(row)} className="cell-links text-primary fw-bold">
          Details
        </span>
      </div>
    },
    {
      name: "Provider",
      selector: row => row.Provider,
      sortable: true
    },
    {
      name: "Agent Type",
      selector: row => row.AgentType,
      sortable: true
    },
    {
      name: "Agency",
      selector: row => row.AgentId,
      sortable: true
    },
    {
      name: "Trip Type",
      selector: row => row.TripType,
      sortable: true
    },
    {
      name: "Star Rating",
      selector: row => row.Star,
      sortable: true
    },
    {
      name: "Applied On",
      selector: row => row.AppliedOn,
      sortable: true
    },
    {
      name: "Markup Type",
      selector: row => row.MarkupType,
      sortable: true
    },
    {
      name: "Markup Amount",
      selector: row => row.MarkupAmount,
      sortable: true
    },
    {
      name: "Submit By",
      selector: row => row.SubmitBy,
      sortable: true
    }
  ];

  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent
          onFilter={e => setFilterText(e.target.value)}
          onClear={handleClear}
          filterText={filterText}
        />
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
      />
    </>
  )
}

export default ViewHotelMarkup;
