import axios from "axios";
import React from "react";
import {useForm} from "react-hook-form";
import {ErrorMessage} from "@hookform/error-message"
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../utils/Common";

const EditHotelMarkup = ({detailEdit, ReadHotelMarkup}) => {
  // console.log(detailEdit);

  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm({defaultValues: detailEdit});

  async function onSubmit(data) {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/hotel-markup/" + data.ID;

      let res = await axios.put(url, data, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        }
      });

      if (res.status === 200) {
        if (res.data.error) {
          errorToast(res.data.message);
        } else {
          successToast(res.data.message);
          ReadHotelMarkup();
        }
      }
    } catch (e) {
      errorToast(e.message);
    }
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="row">
          <div className="col-6 text-end">
            <p>Trip Type:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.TripType}</p>
          </div>

          <div className="col-6 text-end">
            <p>Star Rating:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.Star}</p>
          </div>

          <div className="col-6 text-end">
            <p>Agent Id:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.AgentId}</p>
          </div>

          <div className="col-6 text-end">
            <p>Agent Type:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.AgentType}</p>
          </div>

          <div className="col-6 text-end">
            <p>Provider:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.Provider}</p>
          </div>

          <div className="col-6 text-end">
            <p>Markup Type:</p>
          </div>
          <div className="col-6 mb-3">
            <select name="MarkupType" id="MarkupType" className="form-select"
                    {...register("MarkupType", {required: "This is required."})}>
              <option value="F">Fixed</option>
              <option value="P">Percentage</option>
            </select>

            <ErrorMessage errors={errors} name="MarkupType"
                          render={({message}) => <p className="text-danger">{message}</p>}/>
          </div>

          <div className="col-6 text-end">
            <p>Markup Amount:</p>
          </div>
          <div className="col-6 mb-3">
            <input type="text" className="form-control" id="MarkupAmount" name="MarkupAmount"
                   {...register("MarkupAmount", {
                     required: "This is required.",
                     pattern: {
                       value: /^[0-9]+$/,
                       message: "Only numbers are allowed."
                     }
                   })}/>

            <ErrorMessage errors={errors} name="MarkupType"
                          render={({message}) => <p className="text-danger">{message}</p>}/>
          </div>

          <div className="col-6 text-end">
            <p>Applied On:</p>
          </div>
          <div className="col-6 mb-3">
            <select name="AppliedOn" id="AppliedOn" className="form-select"
                    {...register("AppliedOn", {required: "This is required."})}>
              <option value="Total">Total</option>
              <option value="PerRoom/PerNight">Per Room/Per Night</option>
            </select>

            <ErrorMessage errors={errors} name="AppliedOn"
                          render={({message}) => <p className="text-danger">{message}</p>}/>
          </div>

          <div className="col-6 text-end">
            <p>Submit By:</p>
          </div>
          <div className="col-6">
            <p>{detailEdit?.SubmitBy}</p>
          </div>

          <div className="col-6 offset-6">
            <button className="btn btn-primary px-5">Update</button>
          </div>
        </div>
      </form>
    </>
  )
}

export default EditHotelMarkup;
